    <!--      >{{pageNumber}}</div>-->
    <!--    </div>-->
  </div>

</template>

<script>
import PostList from "@/—Åomponents/PostList";
import PostForm from "@/—Åomponents/PostForm";
import MyButton from "@/—Åomponents/UI/MyButton";
import axios from 'axios';
import MySelect from "@/—Åomponents/UI/MySelect";
import MyInput from "@/—Åomponents/UI/MyInput";
import VIntersection from "@/directives/VIntersection";

export default { // –≤ —Å–∫—Ä–∏–ø—Ç–µ –ø–æ –¥–µ—Ñ–æ–ª—Ç—É —ç–∫—Å–ø–æ—Ä—Ç–∏—Ä—É–µ—Ç—Å—è –æ–±—ä–µ–∫—Ç
  components: {
    PostList, PostForm, MyButton, MySelect, MyInput, VIntersection
  },
  data() {   //–ø–æ–ª–µ
    return {
      posts: [//–º–æ–¥–µ–ª—å
      ],
      dialogVisible: false,
      isPostLoading: false,
      selectedSort: '',
      searchQuery: '',
      page: 1,
      limit: 10,
      totalPages: 0,
      sortOptions: [
        {value: 'title', name: '–ü–æ –Ω–∞–∑–≤–∞–Ω–∏—é'},
        {value: 'body', name: '–ü–æ –æ–ø–∏—Å–∞–Ω–∏—é'},
        // {value: id, name: '–ü–æ ID'}
      ]
    }
  },
  methods: { //–ø–æ–ª–µ
    createPost(post) { //–±—Ä–∞–ª —ç–∫–∑–µ–º–ø–ª—è—Ä –∏–∑ —Ç–µ–∫—É—â–µ–π –º–æ–¥–µ–ª–∏
      this.posts.push(post)
      this.dialogVisible = false
    },
    removePost(post) {
      this.posts = this.posts.filter(p => p.id !== post.id)
    },
    showDialog() {
      this.dialogVisible = true;
    },
    // changePage(pageNumber){
    //   this.page=pageNumber
    //   this.fetchPosts()
    // },
    async fetchPosts() {
      try {
        this.isPostLoading = true
        const response = await axios.get('https://jsonplaceholder.typicode.com/posts?', {
          params:{
            _page: this.page,
            _limit: this.limit
          }
        } );
        this.totalPages = Math.ceil(response.headers['x-total-count'] / this.limit)
        this.posts = response.data;
        this.isPostLoading = false
      } catch (e) {
        alert("Error")
      }
    },
    async loadMorePosts() {
      try {
        this.page += 1;
        // this.isPostLoading = true
        const response = await axios.get('https://jsonplaceholder.typicode.com/posts?', {
          params:{
            _page: this.page,
            _limit: this.limit
          }
        } );
        this.totalPages = Math.ceil(response.headers['x-total-count'] / this.limit)
        this.posts = [...this.posts, ...response.data]
        // this.isPostLoading = false
      } catch (e) {
        alert("Error")
      }
    },
  },
  mounted() {
    this.fetchPosts();
    // console.log(this.$refs.observer);
    // const options = {
    //   // root: document.querySelector('#scrollArea'),
    //   rootMargin: '0px',
    //   threshold: 1.0
    // }
    // //—è –Ω–µ –ø–æ–Ω—è–ª, –ø–æ—á–µ–º—É –ø—Ä–∏ —Ç–µ—Ä—è–µ—Ç—Å—è –∫–æ–Ω—Ç–µ–∫—Å—Ç, –ø—Ä–∏ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–∏ function declaration
    // const callback = (entries, observer) => {
    //   if(entries[0].isIntersecting && this.page < this.totalPages) {
    //     this.loadMorePosts()
    //     console.log(entries)
    //   }
    // };
    // const observer = new IntersectionObserver(callback, options);
    // observer.observe(this.$refs.observer)
  },
  computed: {
    //—è –Ω–µ –ø–æ–Ω–∏–º–∞—é —á—Ç–æ –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç —Ç—É—Ç. —Ö–µ–ª–ø !
    sortedPosts() {
      return [...this.posts].sort((post1, post2) => post1[this.selectedSort]?.localeCompare(post2[this.selectedSort])
      )
    },
    sortedAndSearchPosts() {
      return this.sortedPosts.filter(post => post.title.toLowerCase().includes(this.searchQuery.toLowerCase()))
    }
  },
  watch: {
    // page(){
    //   this.fetchPosts()
    // }
  }
}
