<template>
  <select @change="changeOption">
    <option disabled value="">–í—ã–±–µ—Ä–∏—Ç–µ –∏–∑ —Å–ø–∏—Å–∫–∞</option>
    <option
        v-for="option in options"
        :key="option.value"
